import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Setup from '../views/Setup'
import Quiz from '../views/Quiz'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/setup',
    name: 'setup',
    component: Setup
  },
  {
    path: '/quiz/:nama',
    name: 'quiz',
    component: Quiz
  },
  {
    path: '*',
    name: 'undefined',
    component: Home
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
