import Vue from 'vue'
import Vuex from 'vuex'
import _ from 'lodash'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    question: [],
    index: 0,
    score: []
  },
  getters: {
    getIndex: state => {
      return state.index
    },
    getScore: state => {

        return state.score
      
      
    },
    questionLength: state => {
      if(!state.question[0]){
        return null
      }else{
        return state.question.length
      }
      
    },
    getQuest: state => {
      let a = state.question[state.index]
      if(!a){
        return null
      }
      let answer = [...a.incorrect_answers, a.correct_answer]
      let shuffleAnswer = _.shuffle(answer)
      let currentAnswer = shuffleAnswer.indexOf(a.correct_answer)
      return {
        question: a.question,
        answer: shuffleAnswer,
        currentAnswer: currentAnswer
        
      }
    }

  },
  mutations: {
    setQuest: (state, val)=>{
      state.question = val
    },
    incrementIndex: (state) => {
      state.index++
    },
    resetState: (state) => {
      state.index = 0

    },
    setScores: (state, val)=>{
      state.score = val
    }
   
  },
  actions: {
    getQuest: ({commit}, val) => {
      fetch(`https://opentdb.com/api.php?amount=${val.jmlSoal}&category=${val.isiKategori}&type=multiple`).then((res)=>{
         return res.json()
      }).then((res)=>{
        commit('setQuest',res.results)
      })
    },
    setScore: ({commit}, val) => {
      fetch('https://api.backendless.com/D3CB60BB-F73B-58AE-FF68-FAFC3138ED00/B8431CA4-36D3-44DF-AFA4-6027DFC5594E/data/user', {
        method: 'POST',
        body: JSON.stringify(val)
      })
    },
    getScores: ({commit}) => {
      fetch('https://api.backendless.com/D3CB60BB-F73B-58AE-FF68-FAFC3138ED00/B8431CA4-36D3-44DF-AFA4-6027DFC5594E/data/user?pageSize=3&sortBy=score%20desc').then(res =>{
        return res.json()
      }).then(res => {
        commit('setScores', res)
      })
    }
  },
  modules: {
  }
})
