<template>
    <div class="blue lighten-5">
  

        <v-container>
            <v-card flat id="quest" class="py-1"
            shaped
 
            
            >
                <center>
                
                    <v-row align="center" justify="center">
                <v-card-title class="">
                    <div class="subtitle-2">Question No.{{answered + 1}}</div>
                </v-card-title>

                <v-card-text>
                    <v-col cols="12" md="8">
                    <div class="title">{{convert(Question.question)}}</div>
                
                    <div class="border">
                        <v-list shaped>
      <v-subheader>ANSWER</v-subheader>
      <v-list-item-group>
        <v-list-item
          v-for="(answer, index) in Question.answer"
          :key="index"
          color="primary"
          @click="selectedAnswer(index)"
          :class="colorClass(index)"
        >
   
          <v-list-item-content>
            <v-list-item-title v-text="answer"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
                    </div>

                <div class="mt-3">
                <v-btn class="success mr-4" @click="submitAnswer"
                :disabled="selectedIndex === null || submitted === true"
                >Submit</v-btn>
                <v-btn 
                :class="[getIndex + 1 === questionLength ? 'suber' : '']"
                color="blue white--text" @click="Next"
                :disabled="submitted === false"
                >Next</v-btn>
                </div>

                
      
                    </v-col>
                </v-card-text>
                </v-row>
                </center>
      
         
                  <v-chip
      class="ma-2"
      color="green"
      text-color="white"
    >
      <v-avatar
        left
        class="green darken-4"
      >
        {{score * 120}}
      </v-avatar>
      Score
    </v-chip>
    <v-chip
      class="ma-2 float-right"
      color="blue ligthen-2"
      text-color="white"
    >
        {{answered}}/{{questionLength}}
  
      Terjawab
    </v-chip>
     
      
            </v-card>
        </v-container>
        

  
    
   <v-dialog v-model="dialog" persistent>
     
      <v-card>
        <v-container>
          <v-row>
        <v-col cols="12" md="8">
        <v-card-title class="title grey--text">Horeee!!!! Kamu berhasil menyelesaikan quiz nya :)</v-card-title>
        <v-divider></v-divider>
        </v-col>
        <v-col>
          <v-container class="grey--text">
          <div><b>Soal benar</b> : {{score}}</div>
          <div><b>Score</b> : {{score * 120}}</div>
          </v-container>
        </v-col>
        <v-col cols="12" md="8">
        <v-card-text class="subtitle grey--text">Terima kasih telah bermain. Ingin main lagi ?
        </v-card-text>
        </v-col>
        <v-col cols="12">
        <v-card-actions>
          <v-btn color="blue lighten-2" dark depressed to="/setup" @click="resetState()">Main lagi</v-btn>
          <v-btn color="red ligten-3" depressed dark @click="resetState()" to="/">Gamau !</v-btn>
        </v-card-actions>
        </v-col>
        </v-row>
    </v-container>
      </v-card>
      
    </v-dialog>
    










    </div>
   
</template>

<script>
export default {
    data: () => ({
      selectedIndex: null,
      score: 0,
      answered: 0,
      submitted: false,
      dialog: false
    }),
    computed: {
      Question(){
        return this.$store.getters.getQuest
      },
      questionLength(){
        return this.$store.getters.questionLength
      },
      getIndex(){
        return this.$store.getters.getIndex
      },
     
    },
    mounted(){
      if(this.$store.getters.getQuest === null){
        this.$router.push('/setup')
      }
    },
    methods: {
      Next(){
        this.$store.commit('incrementIndex')
        this.selectedIndex = null
        this.submitted = false
        
      },
       convert(unsafe) {
        return unsafe
         .replace(/&amp;/g, "&")
         .replace(/&lt;/g, "<")
         .replace(/&gt;/g, ">")
         .replace(/&#039;/g, "'")
         .replace(/&quot;/g, '"')
    
      },
      selectedAnswer(index){
        this.selectedIndex = index
      },
      resetState(){
        this.$store.commit('resetState')
      },
      submitAnswer(){
        this.submitted = true
        if(this.submitted && this.selectedIndex === this.Question.currentAnswer){
          this.score++
        }
        this.answered++

        if(this.answered === this.questionLength){
        this.dialog = true
        let val = {
          id: Math.floor(Math.random() * 101),
          nama: this.$route.params.nama,
          score: this.score * 120
        }
        this.$store.dispatch('setScore', val)
        this.$store.commit('resetState')

      }
        
      },
      colorClass(index){
      let color = ''
         if(this.submitted && this.Question.currentAnswer === index){
          color = 'light-green accent-3'
           }else if(this.submitted && this.selectedIndex === index && this.Question.currentAnswer !== index){
             color = 'red accent-3'
           }
           return color
    }

}
}
</script>

<style scoped>
.suber{
  display: none;
}
</style>