<template>
<div>
     <v-stepper v-model="e1">
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1">Masukkan nama</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 2" step="2">Generate soal</v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step step="3">Go!</v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card
          class="mb-12"
          color="purple accent-2"
          height="200px"
        >
        
        <center>
            <v-col cols='10'>
            <v-card elevation="23" class="pa-4">
            <h2 class="purple--text">TULISKAN NAMA KAMU</h2>
           <v-text-field
            label="Nama"
            v-model="nama"
            max="10"
            :rules="rules"
            autofocus
          ></v-text-field>
               </v-card>
               </v-col>
        </center>
        
        </v-card>

        <v-btn
          color="purple accent-1"
          @click="e1 = 2"
          :disabled="nama.length <= 3 || nama.length >= 11"
          
        >
          Continue
        </v-btn>

        <v-btn to="/" text>Cancel</v-btn>
      </v-stepper-content>

      <v-stepper-content step="2">
        <v-card
          class="mb-12"
          color="green accent-1"
          
        >
        <center>
            <v-col cols='10'>
            <v-card elevation="23" class="pa-4">
            <h2 class="green--text">GENERATE QUESTIONS</h2>
           <v-row align="center">
      
      <v-col class="d-flex" cols="12" sm="6">
        <v-select
          :items="jmlSoal"
          :value="jmlSoal"
          v-model.number="setup.jmlSoal"
          label="Jumlah soal"
          color="green"
          append-icon="fas fa-caret-down"
          outlined
        ></v-select>
      </v-col>
      <v-col class="d-flex" cols="12" sm="6">
        <v-select
          :items="kategori"
          v-model.number="setup.isiKategori"
          item-text="name"
          item-value="id"
          label="Pilih kategori"
          color="green"
          append-icon="fas fa-caret-down"
          outlined
        ></v-select>
      </v-col>
      <v-col class="d-flex" cols="12" sm="6">
        <v-select
          :items="level"
          v-model="setup.isiLevel"
          item-text="name"
          item-value="id"
          label="Pilih kesulitan"
          color="green"
          append-icon="fas fa-caret-down"
          outlined
        ></v-select>
      </v-col>

      

    </v-row>


               </v-card>
               </v-col>


        </center>
        </v-card>

        <v-btn
          color="light-green accent-3"
          @click="e1 = 3"
          :disabled="setup.isiKategori === null || setup.isiLevel
           === null || setup.jmlSoal === null"
        >
          Continue
        </v-btn>

        <v-btn @click="e1 = 1" text>Cancel</v-btn>
      </v-stepper-content>

      <v-stepper-content step="3">
        <v-card
          class="mb-12"
          color="yellow lighten-3"
      
        >
        <center>
            <v-col cols='10'>
            <v-card elevation="23" class="pa-4">
            <h2 class="orange--text">MULAI JAWAB KUIS!<br>
            <small>Saat ini, soal Quiz hanya tersedia bahasa inggris.</small><br>
            KLIK 'MULAI QUIZ'
            </h2>
               </v-card>
               </v-col>
        </center>
        </v-card>

        <v-btn
          color="amber lighten-1"
          @click="setQuest"
        >
          Mulai Quiz !
        </v-btn>

        <v-btn @click="e1 = 2" text >Cancel</v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>


 <v-dialog v-model="dialog" persistent>
     
      <v-card>
        <V-container >
          <v-col>
          <center>
            Wait a second....<br>
            <v-progress-circular
      indeterminate
      color="primary"
    ></v-progress-circular>
          </center>
          </v-col>
        </V-container>
      </v-card>
      
    </v-dialog>


  </div>
</template>

<script>
export default{
    data () {
      return {
        e1: 1,
        jmlSoal: [10,20,25,30,40,50],
        kategori: [
          {name: 'Film', id: 11},
          {name: 'Music', id: 12},
          {name: 'Japanese Anime & Manga', id: 31},
          {name: 'Video Game', id: 15},
          {name: 'Science & Nature', id: 17},
          {name: 'Science: Computers', id: 18}

        ],
        rules: [
           value => (value && value.length >= 4) || 'Min 4 characters',
           value => (value && value.length <= 10) || 'Max 10 characters'
        ],
        level: [
          {id: 'easy', name: 'Easy'},
          {id: 'medium', name: 'Medium'}
        ],
        nama: '',
        setup: {
          isiKategori: null,
          isiLevel: null,
          jmlSoal: null
        },
        dialog: false
      }
    },
    methods: {
      setQuest() {
        this.dialog = true
        this.$store.dispatch('getQuest', this.setup).then(()=>{
          setTimeout(() => {
            
            this.$router.push({ path: `/quiz/${this.nama}` })
          }, 5000);
          
        })
        
      }
    }
}
</script>